
.layout-content[data-v-5e999eb1] {
  margin: 10px 5px;
  min-height: 660px;
  position: relative;
}
.layout-content-main[data-v-5e999eb1] {
  height: 100%;
}
/*fade*/
.fade-enter-active[data-v-5e999eb1],
.fade-leave-active[data-v-5e999eb1] {
  -webkit-transition: opacity 0.28s;
  transition: opacity 0.28s;
}
.fade-enter[data-v-5e999eb1],
.fade-leave-active[data-v-5e999eb1] {
  opacity: 0;
}

/*fade-transform*/
.fade-transform-leave-active[data-v-5e999eb1],
.fade-transform-enter-active[data-v-5e999eb1] {
  -webkit-transition: all .7s;
  transition: all .7s;
}
.fade-transform-enter[data-v-5e999eb1] {
  opacity: 0;
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px);
}
.fade-transform-leave-to[data-v-5e999eb1] {
  opacity: 0;
  -webkit-transform: translateX(30px);
          transform: translateX(30px);
}

/*breadcrumb transition*/
.breadcrumb-enter-active[data-v-5e999eb1],
.breadcrumb-leave-active[data-v-5e999eb1] {
  -webkit-transition: all .5s;
  transition: all .5s;
}
.breadcrumb-enter[data-v-5e999eb1],
.breadcrumb-leave-active[data-v-5e999eb1] {
  opacity: 0;
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}
.breadcrumb-move[data-v-5e999eb1] {
  -webkit-transition: all .5s;
  transition: all .5s;
}
.breadcrumb-leave-active[data-v-5e999eb1] {
  position: absolute;
}



